@font-face {
  font-family: "Montserrat";
  src: url("../../public/assets/fonts/Montserrat/static/Montserrat-Regular.ttf");
}
/*--------- Start Global Styles ---------*/
::-webkit-scrollbar {
  width: 8px;
  height: 6px;
}

::-webkit-scrollbar-thumb {
  background-color: #542e90;
  border-radius: 12px;
}

::-webkit-scrollbar-track {
  background-color: #eee;
}

html {
  font-size: 1rem;
}

#root {
  font-family: "Montserrat", sans-serif;
}

.app.open {
  height: 100vh;
  overflow-y: hidden;
  position: relative;
  z-index: 1;
}
.app.open .overlay {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}

.container {
  width: 60.83%;
  margin: auto;
}

.flex {
  display: flex;
}

.between {
  justify-content: space-between;
}

.items-center {
  align-items: center;
}

.pointer {
  cursor: pointer;
}

input, button {
  border: none;
  outline: none;
}

ul {
  list-style: none;
}

/*--------- End Global Styles ---------*/
/*--------- Start Media Query ---------*/
@media all and (max-width: 575px) {
  html {
    font-size: 0.8rem;
  }

  .container {
    width: 90%;
  }
}
@media all and (min-width: 576px) and (max-width: 767px) {
  html {
    font-size: 0.85rem;
  }

  .container {
    width: 85%;
  }
}
@media all and (min-width: 768px) and (max-width: 991px) {
  html {
    font-size: 0.9rem;
  }

  .container {
    width: 80%;
  }
}
@media all and (min-width: 992px) and (max-width: 1199px) {
  html {
    font-size: 0.9rem;
  }

  .container {
    width: 75%;
  }
}
@media all and (min-width: 1200px) and (max-width: 1919px) {
  html {
    font-size: 0.95rem;
  }

  .container {
    width: 75%;
  }
}
/*--------- End Media Query ---------*/

